import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { ApplicationModel  } from '../models/application.model';

@Component({
  selector: 'app-applications-grid',
  templateUrl: './applications-grid.component.html',
  styleUrls: ['./applications-grid.component.scss']
})
export class ApplicationsGridComponent implements OnInit {

  private applications: ApplicationModel[];

  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.getApplications().subscribe(
      result => this.applications = result
    );
  }

  getApplications(): Observable<ApplicationModel[]> {
    return this.http.get<ApplicationModel[]>('api/applications');
  }
}
